.watch-wrapper {
  min-height: 100vh;
  max-width: 100%;
  width: 80%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.calendar {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  column-gap: 10px;
  display: flex;
}

.date-style {
  color: #9e0202;
  text-shadow: 1px 1px 1px #fff;
  letter-spacing: .1938rem;
  align-items: center;
  font-weight: 900;
  display: flex;
}

.date-day, .date-month, .date, .date-year {
  color: #ebddf1;
  text-shadow: 0 1px 1px #a689b3, 0 -1px 1px #fff;
}

.digital-clock {
  color: #ebddf1;
  text-shadow: 0 1px 1px #a689b3, 0 -1px 1px #fff;
  color: #9e0202;
  text-shadow: 0 1px 1px #a689b3, 0 -1px 1px #fff;
  letter-spacing: .1938rem;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-bottom: 3rem;
  font-size: 4rem;
  display: flex;
}

.clock {
  width: 250px;
  height: 250px;
  border: 5px solid #ebddf1;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: 0 -15px 15px rgba(255, 255, 255, .05), inset 0 -15px 15px rgba(255, 255, 255, .05), 0 15px 15px rgba(166, 137, 179, .3), inset 0 15px 15px rgba(166, 137, 179, .3);
}

.clock:before {
  content: "";
  width: 15px;
  height: 15px;
  z-index: 10000;
  background: #ebddf1;
  border-radius: 50%;
  position: absolute;
  box-shadow: 1px 0 3px rgba(158, 2, 2, .447), -1px 0 3px #fff;
}

.clock .hour, .clock .minute, .clock .second {
  position: absolute;
}

.clock .hour, .hr {
  width: 160px;
  height: 160px;
}

.clock .minute, .mn {
  width: 190px;
  height: 190px;
}

.clock .second, .sc {
  width: 230px;
  height: 230px;
}

.hr, .mn, .sc {
  border-radius: 50%;
  justify-content: center;
  display: flex;
  position: absolute;
}

.hr:before {
  content: "";
  width: 6px;
  height: 95px;
  z-index: 10;
  background: #ead5f4;
  border-radius: 6px 6px 0 0;
  position: absolute;
}

.mn:before {
  content: "";
  width: 4px;
  height: 115px;
  z-index: 11;
  background: #fff;
  border-radius: 6px 6px 0 0;
  position: absolute;
}

.sc:before {
  content: "";
  width: 2px;
  height: 145px;
  z-index: 12;
  background: #9e0202;
  border-radius: 6px 6px 0 0;
  position: absolute;
}

.clock .num {
  width: 100%;
  height: 100%;
  color: #ebddf1;
  text-shadow: 1px 0 3px rgba(158, 2, 2, .447), -1px 0 3px #fff;
  text-align: center;
  font-weight: 700;
  position: absolute;
}

#num1 {
  transform: rotate(90deg);
}

#num2 {
  transform: rotate(180deg);
}

#num3 {
  transform: rotate(270deg);
}

#three {
  display: block;
  transform: rotate(270deg);
}

#six {
  display: block;
  transform: rotate(180deg);
}

#nine {
  display: block;
  transform: rotate(90deg);
}

