.watch-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  max-width: 100%;
  width: 80%;
}

.calendar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  column-gap: 10px;
}

.date-style {
  display: flex;
  align-items: center;
}

.date-style {
  color: #9e0202;
  text-shadow: 1px 1px 1px #fff;
  /* font-size: calc($index * 0.8); */
  font-weight: 900;
  letter-spacing: 0.1938rem;
}

.date-day,
.date-month,
.date,
.date-year {
  color: #ebddf1;
  text-shadow: 0 1px 1px #a689b3, 0 -1px 1px #fff;
}

.digital-clock {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 3rem;
  color: #ebddf1;
  text-shadow: 0 1px 1px #a689b3, 0 -1px 1px #fff;
  color: #9e0202;
  text-shadow: 0 1px 1px #a689b3, 0 -1px 1px #fff;
  /* font-size: calc($index * 2.8); */
  font-size: 4rem;

  letter-spacing: 0.1938rem;
  text-align: center;
}

.clock {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 250px;

  border: 5px solid #ebddf1;
  border-radius: 50%;

  box-shadow: 0 -15px 15px rgba(255, 255, 255, 0.05),
    inset 0 -15px 15px rgba(255, 255, 255, 0.05),
    0 15px 15px rgba(166, 137, 179, 0.3),
    inset 0 15px 15px rgba(166, 137, 179, 0.3);
}

.clock::before {
  content: '';
  position: absolute;
  width: 15px;
  height: 15px;
  background: #ebddf1;
  box-shadow: 1px 0 3px #9e020272, -1px 0 3px #fff;
  border-radius: 50%;
  z-index: 10000;
}

.clock .hour,
.clock .minute,
.clock .second {
  position: absolute;
}

.clock .hour,
.hr {
  width: 160px;
  height: 160px;
}

.clock .minute,
.mn {
  width: 190px;
  height: 190px;
}

.clock .second,
.sc {
  width: 230px;
  height: 230px;
}

.hr,
.mn,
.sc {
  display: flex;
  justify-content: center;
  /* align-items: center; */
  position: absolute;
  border-radius: 50%;
}

.hr::before {
  content: '';
  position: absolute;
  width: 6px;
  height: 95px;
  background: #ead5f4;
  z-index: 10;
  border-radius: 6px 6px 0 0;
}

.mn::before {
  content: '';
  position: absolute;
  width: 4px;
  height: 115px;
  background: #ffff;
  z-index: 11;
  border-radius: 6px 6px 0 0;
}

.sc::before {
  content: '';
  position: absolute;
  width: 2px;
  height: 145px;
  background: #9e0202;
  z-index: 12;
  border-radius: 6px 6px 0 0;
}

.clock .num {
  position: absolute;
  width: 100%;
  height: 100%;
  color: #ebddf1;
  text-shadow: 1px 0 3px #9e020272, -1px 0 3px #fff;
  font-weight: 700;
  text-align: center;
}

#num1 {
  transform: rotate(90deg);
}

#num2 {
  transform: rotate(180deg);
}

#num3 {
  transform: rotate(270deg);
}

#three {
  display: block;
  transform: rotate(270deg);
}

#six {
  display: block;
  transform: rotate(180deg);
}

#nine {
  display: block;
  transform: rotate(90deg);
}
